.MAIN {
    padding: 22px 22px 40px 22px;
}

.MainInnerImg {
    display: block;
    margin-left: auto;
    margin-right: auto;
    border: 2px solid #000;
    width: 95% !important;
    padding: 0;
}

.outer {
    margin: 0;
    padding: 0;
}

.Paper {
    margin-top: 5px;
    margin-bottom: 20px;
    margin-left: auto;
    margin-right: auto;
    width: 70% !important;
    padding: 10px !important;
}

.Alert {
    width: 99% !important;
    font-size: 25px !important
}

