.Main_Widget {
    display: flex;
    justify-content: center;
    padding: 15px;
    flex-wrap: wrap;
    margin: 5px auto;
}

.Value {
    padding: 5px 0
}

.Inner {
    display: flex;
    justify-content: space-between;
    margin: 0;
}

.Icon {
    width: 40px;
    margin: 15px;
}

.Widget {
    width: 250px;
    margin: 10px;
    padding: 20px;
    border-radius: 5px !important;
}

.ContentInner {
    display: flex;
    margin: 0;
    padding: 0
}

@media (min-width: 481px) and (max-width: 767px) {
    .Main_Widget {
        display: grid;
        grid-template-columns: max-content max-content;
        flex-wrap: wrap;
    }
    .Widget {
        width: 250px;
        margin: 20px;
        height: auto;
        padding: 8px;
        border-radius: 5px !important;
    }
}

@media (min-width: 768px) and (max-width: 1024px) {
    .Main_Widget {
        display: flex !important;
        justify-content: center;
        flex-wrap: wrap;
    }
    .Widget {
        width: 250px;
        margin: 20px;
        height: auto;
        padding: 8px;
        border-radius: 5px !important;
    }
}

@media (min-width: 1025px) and (max-width: 1280px) {
    .Main_Widget {
        display: flex !important;
        justify-content: center;
        flex-wrap: wrap;
    }
    .Widget {
        width: 250px;
        margin: 20px;
        height: auto;
        padding: 8px;
        border-radius: 5px !important;
    }
}

@media (min-width: 1281px) {
    .Main_Widget {
        display: grid;
        grid-template-columns: max-content max-content max-content max-content max-content max-content;
    }
    .Widget {
        width: 200px;
        margin: 10px;
        padding: 15px;
        border-radius: 5px !important;
    }
}

.Total {
    color: white !important;
    background-color: #17a2b8 !important;
}

.Active {
